/* navbar */
.navbar.nav-total {
  box-shadow: 0px 4px 6px rgba(147, 147, 147, 0.26);
  box-shadow: var(--firstBoxShadow);
}

.navbar.nav-total .navbar-toggler {
  transition: all 0.25s;
  position: relative;
  width: 40px;
  height: 40px;
  cursor: pointer;
  border: none;
}

.navbar.nav-total .navbar-toggler::before {
  content: '';
  position: absolute;
  width: 30px !important;
  height: 3px !important;
  background-color: #012C57;
  background-color: var(--mainColor);
  transition: all 0.25s;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  box-shadow: 0 10px 0 #012C57;
  box-shadow: 0 10px 0 var(--mainColor);
}

.navbar.nav-total .navbar-toggler.convert::before {
  -webkit-transform: translateY(0px) rotate(45deg);
          transform: translateY(0px) rotate(45deg);
  box-shadow: 0 0 0 #012C57;
  box-shadow: 0 0 0 var(--mainColor);
}

.navbar.nav-total .navbar-toggler::after {
  content: '';
  position: absolute;
  width: 30px !important;
  height: 3px !important;
  background-color: #012C57;
  background-color: var(--mainColor);
  transition: all 0.25s;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}

.navbar.nav-total .navbar-toggler.convert::after {
  -webkit-transform: translateY(0px) rotate(-45deg);
          transform: translateY(0px) rotate(-45deg);
}

.navbar.nav-total .navbar-toggler:focus,
.navbar.nav-total .navbar-toggler:hover {
  box-shadow: none;
}

.navbar.nav-total .navbar-nav li {
  margin: auto 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar.nav-total .navbar-nav li a {
  color: #ACACAC;
  /* color: var(--secondColor); */
  position: relative;
  font-weight: 600;
  font-size: 16px;
  transition: all 0.25s;
  text-align: center;
}

.navbar.nav-total .navbar-nav li a::before {
  content: '';
  position: absolute;
  width: 50%;
  opacity: 0;
  height: 1.5px;
  background-color: #012C57;
  background-color: var(--mainColor);
  /* background-color: var(--white); */
  bottom: 2px;
  right: 100%;
  transition: all 0.25s;
}

.text-en{
  text-align: left !important;
}

.text-ar{
  text-align: right !important;
}

.active {
  font-weight: 700 !important;
}

.active,
.navbar-nav a:focus,
.navbar-nav a:hover {
  color: #012C57 !important;
  color: var(--mainColor) !important;
  /* color: var(--white) !important; */
}

.active::before,
.navbar-nav a:focus::before,
.navbar-nav a:hover::before {
  right: 0% !important;
  -webkit-transform: translateX(-50%) !important;
          transform: translateX(-50%) !important;
  opacity: 0.7 !important;
}

