/* dashboard */

/* sidebar */

.sidebar {
  position: fixed;
  /* width: 300px; */
  height: 100%;
  background-color: #012C57;
  background-color: var(--mainColor);
  padding-left: 10px;
  overflow-x: hidden;
  transition: all 0.5s;
}

.sidebar .menu-links {
  position: relative;
}

.sidebar .menu-links ul>div {
  margin-bottom: 25px;
  margin-top: 15px;
  position: relative;
}

.sidebar .menu-links ul>div .closeSidebar {
  position: absolute;
  top: 0;
  right: 15px;
  width: 50px;
  height: 50px;
  display: none;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 2rem;
  color: #8a9fbe;
  color: var(--mainSidebar);
}

.sidebar .menu-links .cont-imgFooter {
  /* width: 130px;
  height: 110px; */
  transition: all 0.5s;
}

.sidebar .menu-links ul {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.sidebar .menu-links ul li {
  position: relative;
  width: 100%;
  list-style: none;
  cursor: pointer;
  border-bottom-left-radius: 30px;
  border-top-left-radius: 30px;
}

.sidebar .menu-links ul li:hover {
  background-color: #8a9fbe;
  background-color: var(--mainSidebar);
}

.sidebar .menu-links ul li a,
.sidebar .menu-links ul li div.logOut {
  position: relative;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  width: 100%;
  text-decoration: none;
  color: #8a9fbe;
  color: var(--mainSidebar);
}

.sidebar .menu-links ul li:hover a,
.sidebar .menu-links ul li:hover div.logOut {
  color: #012C57;
  color: var(--mainColor);
}

.sidebar .menu-links ul li a .icon,
.sidebar .menu-links ul li div.logOut .icon {
  position: relative;
  display: block;
  min-width: 60px;
  height: 57px;
  line-height: 45px;
  text-align: center;
}

.sidebar .menu-links ul li a .icon svg,
.sidebar .menu-links ul li div.logOut .icon svg {
  font-size: 1.75rem;
}

.sidebar .menu-links ul li a .title,
.sidebar .menu-links ul li div.logOut .title {
  position: relative;
  display: block;
  padding: 0 10px;
  text-align: center;
  font-size: 18px;
  font-weight: 500;
  height: 57px;
  line-height: 55px;
  transition: all 0.3s;
}

.sidebar .menu-links ul li a.active .title{
  font-weight: 600;
}

.sidebar .menu-links ul li:hover a::before,
.sidebar .menu-links ul li:hover div.logOut::before {
  content: '';
  position: absolute;
  right: 0;
  top: -50px;
  width: 50px;
  height: 50px;
  background-color: transparent;
  background-color: var(--transparent);
  border-radius: 50%;
  box-shadow: 35px 35px 0 10px #8a9fbe;
  box-shadow: 35px 35px 0 10px var(--mainSidebar);
  pointer-events: none;
}

.sidebar .menu-links ul li:hover a::after,
.sidebar .menu-links ul li:hover div.logOut::after {
  content: '';
  position: absolute;
  right: 0;
  bottom: -50px;
  width: 50px;
  height: 50px;
  background-color: transparent;
  background-color: var(--transparent);
  border-radius: 50%;
  box-shadow: 35px -35px 0 10px #8a9fbe;
  box-shadow: 35px -35px 0 10px var(--mainSidebar);
  pointer-events: none;
}

.sidebar a.active {
  color: #012C57 !important;
  color: var(--mainColor) !important;
  background-color: #8a9fbe !important;
  background-color: var(--mainSidebar) !important;
  border-bottom-left-radius: 30px;
  border-top-left-radius: 30px;
}

.sidebar a.active::after,
.sidebar a.active::before{
  box-shadow: none !important;
}


/* outlet */

/* navbar */

.dashboard .main{
  position: absolute;
  /* width: calc(100% - 300px);
  left: 300px; */
  min-height: 100vh;
  transition: all 0.5s;
  padding: 0 10px;
}

.dashboard .main .topbar {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dashboard .main .topbar .toggle-topbar {
  position: relative;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  color: #012C57;
  color: var(--mainColor);
  cursor: pointer;
}

.dashboard .main .topbar .search-topbar {
  position: relative;
  margin: 0 10px;
  transition: all 0.5s;
}

.dashboard .main .topbar .search-topbar .group {
  position: relative;
  width: 100%;
}

.dashboard .main .topbar .search-topbar .group input {
  width: 100%;
  height: 40px;
  border-radius: 20px;
  padding: 20px;
  font-size: 18px;
  outline: none;
  border: 1px solid #012c574e;
}

.dashboard .main .topbar .search-topbar .group svg {
  position: absolute;
  top: 50%;
  right: 15px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 1.25rem;
  color: #012C57 !important;
  color: var(--mainColor) !important;
}

.dashboard .main .topbar .user-img {
  position: relative;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.5s;
}

.dashboard .main .topbar .user-img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}


